export default {
	name: 'ShopCart',
	data() {
		return {
			goodsList: []
		};
	},
	props: {
		shopInfo: {
			type: Object,
			default: () => {}
		}
	},
	watch:{
	},
	created() {
		this.getGoodsList()
	},
	computed: {
		totalPrice() {
			let total = 0;
			this.goodsList.forEach(item => {
				total += item.food_price * item.foot_count;
			});
			return total;
		},
		totalCount() {
			let count = 0;
			this.goodsList.forEach(item => {
				count += item.foot_count;
			});
			return count;
		},
		minPrice() {
			if(this.shopInfo) {
				return this.shopInfo.rst.float_minimum_order_amount
			}
		},
		payClass() {
			if (this.totalPrice < this.minPrice) {
				return 'not-enough';
			} else {
				return 'enough';
			}
		},
		payDesc() {
			if (this.totalPrice === 0) {
				return `¥${this.minPrice}元起送`;
			} else if (this.totalPrice < this.minPrice) {
				let diff = this.minPrice - this.totalPrice;
				return `还差${diff}`;
			} else {
				return '去结算';
			}
		}
	},
	methods:{
		getGoodsList() {
			uni.getStorage({
				key: 'goodsList',
				success: res => {
					this.goodsList = res.data;
				}
			});
		}
	}
};
